.search-filters {
  display: flex;
  flex-wrap: wrap;

  @include lg {
    margin-left: rem(-10);
    margin-right: rem(-10); }

  &__col {
    width: rem(250);
    margin-right: rem(10);
    margin-bottom: rem(10);

    @include lg {
      width: 33.33333%;
      margin-right: 0;
      padding-left: rem(10);
      padding-right: rem(10); }

    @include xs {
      width: 100%; } } }

.search {
  position: relative;

  &__input {
    width: 100%;
    height: $container--lg-padding;

    border: 1px solid  $lighten;
    background-color: $white;
    color: $light;

    padding: rem(20) rem(30);
    padding-right: rem(100);
    font-size: em(14);

    @include lg {
      height: $header-height-lg;
      padding: rem(10) rem(15);
      padding-right: rem(50); } }

  &__btn {
    position: absolute;
    z-index: 1;
    top: 0;
    right: 0;

    height: 100%;
    width: rem(90);

    color: $accent;
    background-color: transparent;

    @include lg {
      width: rem(50); }

    .icon {
      font-size: rem(30);
      @include lg {
        font-size: rem(20); } } } }
