.testimonials {
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;

    margin-bottom: rem(30);

    @include sm {
      display: block; } }

  &__top-rating {
    display: flex;
    align-items: center;


    @include sm {
      margin-bottom: rem(10);
      flex-wrap: wrap; }

    small {
      font-size: em(12);
      color: $light-dark;
      display: block;
      border-left: 1px solid currentColor;
      padding-left: rem(10);

      @include sm {
        width: 100%;
        padding-left: 0;
        border-left: 0; } } } }

.testimonial {
  background-color: $white;
  padding: rem(25) rem(30);
  color: $light-dark;

  @include lg {
    padding: rem(20); }

  &__rating {
    margin-bottom: rem(20); }

  &__title {
    margin-bottom: rem(8); }

  &__text {
    font-size: em(14);
    @include notlast {
      margin-bottom: rem(10); } } }

.add-comment {
  &__title {
    margin-bottom: rem(15); }

  &__field {
    display: block;

    background-color: transparent;
    border: 1px solid $c-border;

    width: 100%;
    min-height: rem(140);

    font-size: em(18);
    color: $light-dark;

    padding: rem(15) rem(20);
    margin-bottom: rem(10);

    @include placeholder {
      color: inherit; } } }

.likes {
  $this: &;

  font-size: em(14);

  &__btn {
    display: inline-block;
    vertical-align: middle;
    &--like {
      color: #00b100;
      margin-right: rem(10); }
    &--dislike {
      color: #f43939;
      #{$this}__icon {
        transform: scaleY(-1);
        position: relative;
        top: .3em; } } }

  &__icon {
    fill: $light-dark; } }


