.aside-container {
  @extend .grid;

  &__aside, &__content {
    @extend .col; }

  &__aside {
    width: percentage(255 / 1650);
    @include r(1749) {
      width: rem(300);
      transform: translate(calc(-100% - #{$container--lg-padding}), 0);
      position: fixed;
      top: 0;
      left: 0;
      z-index: 10; } }

  &__content {
    width: percentage((1650 - 255) / 1650);
    @include r(1749) {
      width: 100%; } } }

.aside {
  &__top {
    color: $white;
    background-color: $accent;
    padding: rem(15) rem(25); } }
