.catalog-accordion {
  $this: &;

  &__btn {
    position: relative;
    display: block;
    width: 100%;
    text-align: left;
    background-color: $white;
    padding: rem(15) rem(25);

    @include before {
      @include icon-arrow(.5, .5, 0.1, $light, -45, em);
      position: absolute;
      z-index: 1;
      right: rem(25);
      top: 50%;
      transform: translate(0, -50%) rotate(-45deg); }

    &--sub {
      background-color: $lightest;
      padding: rem(5) rem(25);
      border-bottom: 1px solid $white; } }

  &__sublist {
    font-size: em(14); }

  &__link {
    display: block;
    padding: rem(5) rem(25);

    background-color: $white;
    color: #747474;
    border-bottom: 1px solid $lightest;

    @include hover {
      text-decoration: underline; } } }
