.s-descr {
  padding-top: rem(110);
  padding-bottom: rem(90);
  @include lg {
    padding-top: rem(80);
    padding-bottom: rem(80); }
  @include md {
    padding-top: rem(60);
    padding-bottom: rem(60); }
  @include sm {
    padding-top: rem(30);
    padding-bottom: rem(30); } }

.insta-items {
  padding-left: rem(30);
  padding-right: rem(30);
  padding-bottom: rem(30);

  &__inner {
    display: flex;
    flex-wrap: wrap; }

  &__item {
    width: percentage(1 / 6);
    @include lg {
      width: 20%; }
    @include md {
      width: 25%; }
    @include sm {
      width: 50%; } } }

.insta-item {
  $this: &;

  display: block;

  padding-top: 15%;
  position: relative;
  color: $white;
  font-size: em(12);
  text-align: center;
  background-color: $light;
  @include cover;
  @include before {
    @include coverdiv;
    background-color: rgba($black, .7);
    @include hidden;
    @include tr(opacity .3s, visibility .3s); }

  &:hover {
    #{$this}__inner, &::before {
      @include visible; } }

  @include lg {
    padding-top: 20%; }
  @include md {
    padding-top: 25%; }
  @include sm {
    padding-top: 40%; }


  &__inner {
    display: block;
    @include vcenter;
    z-index: 1;
    @include hidden;
    @include tr(opacity .3s, visibility .3s); }

  &__icon {
    display: block;
    max-width: rem(45);
    font-size: 0;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: rem(15); }

  &__title {
    text-decoration: underline; } }

