.s-two-colored {
  $this: &;

  position: relative;
  &__top {
    padding-top: rem(90);
    padding-bottom: rem(70);
    @include lg {
      padding-top: rem(50);
      padding-bottom: rem(50); } }
  &__bottom {
    padding-top: rem(115);
    padding-bottom: rem(140);

    background-color: $accent;
    color: $white;

    @include lg {
      padding-top: rem(90);
      padding-bottom: rem(90); } }

  &__img {
    @include cover;
    background-color: $dark;

    position: absolute;
    z-index: 1;
    right: $container--lg-padding;
    bottom: 0;

    padding-top: 37%;

    width: percentage(670 / 1740);

    @include lg {
      right: 0; }

    @include md {
      display: none; } }

  &--bottom-offset {
    margin-bottom: rem(30);
    #{$this}__img {
      &.lazy--loaded {
        bottom: rem(-30); } } } }

.s-text-word {
  padding-top: vw(340);
  padding-bottom: rem(90); }

.s-dude {
  @include cover;
  background-color: $dark;
  color: $white;

  padding-top: rem(110);
  padding-bottom: rem(110);
  margin-top: rem(190);

  position: relative;

  > .container {
    position: relative;
    z-index: 1; }

  &__img {
    position: absolute;
    bottom: 0;
    left: 10%;
    z-index: 0; } }

.s-full {
  @include windowHeight($header-height);
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;

  background-color: $dark;
  color: $white;

  @include lg {
    @include windowHeight($header-height-lg * 2); }

  &__icon {
    width: rem(54);
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: rem(20); }

  &__title {
    margin-bottom: rem(10); }

  &__subttl {
    margin-bottom: rem(15); } }

