.cabinet {
  background-color: $white;

  &__hero {
    background-color: $dark;
    color: $white; }

  &__breadcrumbs {
    margin-bottom: rem(30); }

  &__hero-inner {
    display: flex;
    align-items: center;
    justify-content: space-between;

    padding-top: rem(15);
    padding-bottom: rem(15);

    @include r(1559) {
      display: block; } }

  &__hero-left {
    padding-right: rem(15);
    width: 60%;
    @include r(1559) {
      width: auto; } }

  &__hero-right {
    width: 40%;
    @include r(1559) {
      width: auto; } }

  &__hero-title {
    margin-bottom: rem(25); }

  &__block {
    @include notlast {
      margin-bottom: rem(45);
      @include lg {
        margin-bottom: rem(30); }
      @include md {
        margin-bottom: rem(15); } } }

  &__hero-btns {
    display: flex;
    justify-content: flex-end;
    @include r(1559) {
      justify-content: flex-start; }
    @include xs {
      display: block; } }

  &__hero-btn {
    @include notlast {
      margin-right: rem(15);
      @include xs {
        margin-right: 0;
        margin-bottom: rem(15); } } } }

.cabinet-meta {
  display: flex;
  flex-wrap: wrap;

  &__left, &__right {
    width: percentage(690 / 1650);

    @include xl {
      width: 50%; }

    @include sm {
      width: 100%;
      margin-bottom: rem(15);
      padding-left: 0;
      padding-right: 0; } }

  &__left {
    @include xl {
      padding-right: rem(15); } }

  &__right {
    margin-left: percentage(75 / 1650);
    @include xl {
      margin-left: 0;
      padding-left: rem(15); } }

  &__order-item {
    border-bottom: 1px solid  $lighten;
    padding-top: rem(15);
    padding-bottom: rem(15); } }

.cabinet-meta-block {
  $this: &;

  &__title {}


  &__block {
    display: flex;
    flex-wrap: wrap;
    @include notlast {
      margin-bottom: rem(25); }

    @include md {
      display: block; } }

  &__block-title {
    width: percentage(180 / 690);
    padding-right: rem(10);

    @include md {
      width: auto;
      padding-right: 0;
      margin-bottom: rem(15); } }

  &__block-meta {
    width: percentage((690 - 180) / 690);
    @include md {
      width: auto; } }

  &--block-bdb {
    #{$this}__block-meta {
      border-bottom: 1px solid  $lighten; } } }



